import React from 'react';
export const Scientific = (props) => {
  return (
    <div className="scidiv">
      <button className="scibtn">Inv</button>
      <button name="sin" className="scibtn" onClick={props.callScientific}>sin</button>
      <button name="^" className="scibtnn" onClick={props.opr}>x<sup>y</sup></button><br />
      <button name="√" className="scibtn" onClick={props.callScientific}>√</button>
      <button name="cos" className="scibtn" onClick={props.callScientific}>cos</button>
      <button name="log" className="scibtn" onClick={props.callScientific}>log</button><br />
      <button name="∛" className="scibtn" onClick={props.callScientific}>∛</button>
      <button name="tan" className="scibtn" onClick={props.callScientific}>tan</button>
      <button name="π" className="scibtn" onClick={props.callScientific}>π</button>
    </div>
  )

}