import React, { Component } from 'react';
import { Input } from '../Component/Calcinput';
import { Operation } from '../Component/Calcoperation';
import { Scientific } from '../Component/scientific';
export class Calc extends Component {
  constructor(props) {
    super(props)
    this.addopr = ""
    this.subopr = ""
    this.mulopr = ""
    this.divopr = ""
    this.modopr = ""
    this.ScientificValue = ""
    this.ScientificResult = ""
    this.opr = ""
    this.inputFirst = ""
    this.inputSecond = ""
    this.finalResults = ""
    this.temp = ""
    this.addFlag = "false";
    this.isRunAdd = "false";
    this.equalopr = "";
    this.toggle = true
    this.state = {
      hide: this.toggle,
      flag: "",
      result: "",
      inputFirst: "",
      inputSecond: "",
      finalresult: "",
      opr: this.opr
    }
  }


  calculate() {
    try {
      this.setState({
        result: eval(this.state.result)
      })
    } catch (error) {
      this.setState({
        result: "error"
      })
    }
  }
  backSpace() {
    this.setState({
      result: this.state.result.slice(0, -1)
    })
  }
  backSpace2() {
    this.setState({
      inputFirst: this.state.inputFirst.slice(0, -1),
      inputSecond: this.state.inputSecond.slice(0, -1),
      finalresult: this.state.finalresult.slice(0, -1)
    })
  }

  takeData(value) {
    if (value === "=") {
      this.calculate()
    } else {
      this.setState({
        result: this.state.result + value
      })
    }
    if (value === "C") {
      this.setState({
        result: ""
      })
    }
  }

  clearData() {
    this.setState({
      result: ""
    })
  }
  clearData2() {
    this.ScientificValue = ""
    this.ScientificResult = ""
    this.opr = ""
    this.inputFirst = ""
    this.inputSecond = ""
    this.finalResults = ""
    this.temp = ""
    this.addFlag = "false";
    this.isRunAdd = "false";
    this.equalopr = "";
    this.setState({
      inputFirst: "",
      inputSecond: "",
      finalresult: ""
    })
  }


  equal(eopr) {
    if (this.ScientificValue === "sin") {
      this.sin();
    }
    else if (this.ScientificValue === "√") {
      this.sqrt();
    }
    else if (this.ScientificValue === "cos") {
      this.cos();
    }
    else if (this.ScientificValue === "log") {
      this.log();
    }
    else if (this.ScientificValue === "∛") {
      this.cbrt();
    }
    else if (this.ScientificValue === "tan") {
      this.tan();
    }
    else if (this.ScientificValue === "π") {
      this.pi();
    }
    this.equalopr = eopr;
    if (this.opr === "+") {
      if (this.addopr === "+") {
        this.add();
        this.addopr = ""
      } else if (this.subopr === "-") {
        this.sub();
        this.subopr = ""
      } else if (this.mulopr === "*") {
        this.mul();
        this.mulopr = ""
      } else if (this.divopr === "/") {
        this.div();
        this.divopr = ""
      } else if (this.modopr === "%") {
        this.mod();
        this.modopr = ""
      } else
        if (this.equalopr === "=") {
          this.add();
        }
    } else if (this.opr === "-") {
      if (this.addopr === "+") {
        this.add();
        this.addopr = ""
      } else if (this.subopr === "-") {
        this.sub();
        this.subopr = ""
      } else if (this.mulopr === "*") {
        this.mul();
        this.mulopr = ""
      } else if (this.divopr === "/") {
        this.div();
        this.divopr = ""
      } else if (this.modopr === "%") {
        this.mod();
        this.modopr = ""
      } else
        if (this.equalopr === "=") {
          this.sub();
        }
    }
    else if (this.opr === "*") {
      if (this.addopr === "+") {
        this.add();
        this.addopr = ""
      } else if (this.subopr === "-") {
        this.sub();
        this.subopr = ""
      } else if (this.mulopr === "*") {
        this.mul();
        this.mulopr = ""
      } else if (this.divopr === "/") {
        this.div();
        this.divopr = ""
      } else if (this.modopr === "%") {
        this.mod();
        this.modopr = ""
      } else
        if (this.equalopr === "=") {
          this.mul();
        }
    }
    else if (this.opr === "/") {
      if (this.addopr === "+") {
        this.add();
        this.addopr = ""
      } else if (this.subopr === "-") {
        this.sub();
        this.subopr = ""
      } else if (this.mulopr === "*") {
        this.mul();
        this.mulopr = ""
      } else if (this.divopr === "/") {
        this.div();
        this.divopr = ""
      } else if (this.modopr === "%") {
        this.mod();
        this.modopr = ""
      } else
        if (this.equalopr === "=") {
          this.div();
        }
    } else if (this.opr === "%") {
      if (this.addopr === "+") {
        this.add();
        this.addopr = ""
      } else if (this.subopr === "-") {
        this.sub();
        this.subopr = ""
      } else if (this.mulopr === "*") {
        this.mul();
        this.mulopr = ""
      } else if (this.divopr === "/") {
        this.div();
        this.divopr = ""
      } else if (this.modopr === "%") {
        this.mod();
        this.modopr = ""
      } else
        if (this.equalopr === "=") {
          this.mod();
        }
    } else if (this.opr === "^") {
      this.power();
    }
    if (this.state.flag === "true") {
      console.log("fdfjdk")
      this.setState({
        inputFirst: this.finalResults,
        flag: "false"
      })
    } else {
      this.setState({
        inputFirst: this.finalResults
      })
    }
    if (this.equalopr === "=") {
      this.setState({
        inputSecond: ""
      })

    }
  }
  operator(event) {
    if (event.target.name === "+") {
      this.addopr = event.target.name
    } else if (event.target.name === "-") {
      this.subopr = event.target.name
    } else if (event.target.name === "*") {
      this.mulopr = event.target.name
    }
    else if (event.target.name === "/") {
      this.divopr = event.target.name
    }
    else if (event.target.name === "%") {
      this.modopr = event.target.name
    }
    this.inputSecond = ""
    if (this.state.flag === "true") {
      console.log("op");
      if (this.ScientificValue === "√" || this.ScientificValue === "∛" || this.ScientificValue === "sin" || this.ScientificValue === "cos" || this.ScientificValue === "tan" || this.ScientificValue === "log") {
        console.log("aaaa")
        this.equal();
        this.opr = event.target.name;
        console.log(this.opr)
        this.setState({
          opr: this.opr,
          finalresult: this.finalResults + this.opr
        })
        this.ScientificValue = ""
      } else {
        console.log("rrrra")
        this.opr = event.target.name;
        this.setState({
          opr: this.opr,
          finalresult: this.inputFirst + this.opr
        })
      }
      if (this.addFlag === "true") {
        this.equal();
        this.setState({
          inputSecond: ""
        })
        this.opr = event.target.name;
        this.setState({
          opr: this.opr,
          finalresult: this.finalResults + this.opr
        })
        this.addFlag = "false"
      } else {
        this.addFlag = "true"
      }
    } else {
      console.log("nna")
      if (this.equalopr !== "=") {
        if (this.addFlag === "false") {
          // this.equal();
          this.setState({
            inputSecond: ""
          })
        }
      }
      // else {
      //   this.equalopr = ""
      // }
      this.opr = event.target.name;
      this.setState({
        opr: this.opr,
        finalresult: this.finalResults + this.opr
      })

      if (this.equalopr !== "=") {
        console.log("bbbbb")
        console.log(this.equalopr)
        if (this.ScientificValue === "") {
          console.log("bbbbb")
          this.equal();
          this.opr = event.target.name;
          this.setState({
            opr: this.opr,
            finalresult: this.finalResults + this.opr
          })
        }
      } else {
        console.log(this.equalopr)
        console.log("mmmmm")
        this.opr = event.target.name;
        this.setState({
          opr: this.opr,
          finalresult: this.finalResults + this.opr
        })
      }
      this.equalopr = ""
    }
  }
  takeData2(value) {
    if (this.opr === "") {
      this.inputFirst = this.inputFirst + value
      this.setState({
        inputFirst: this.inputFirst,
        finalresult: this.inputFirst,
        flag: "true"
      })
    }
    if (this.opr === "+" || this.opr === "-" || this.opr === "*" || this.opr === "/" || this.opr === "%" || this.opr === "^") {
      if (this.state.flag === "true") {
        this.inputSecond = this.inputSecond + value
        this.temp = value;
        this.setState({
          inputSecond: this.inputSecond,
          finalresult: this.state.finalresult + this.temp
        })
        this.temp = ""
      } else {
        this.inputSecond = this.inputSecond + value
        this.temp = value;
        this.setState({
          inputSecond: this.inputSecond,
          finalresult: this.state.finalresult + this.temp
        })
        this.temp = ""
      }
    }

  }

  add() {
    this.finalResults = parseFloat(this.state.inputFirst) + parseInt(this.state.inputSecond);
    this.setState({
      finalresult: this.finalResults
    })
  }

  sub() {
    this.finalResults = parseFloat(this.state.inputFirst) - parseInt(this.state.inputSecond);
    this.setState({
      finalresult: this.finalResults
    })
  }

  mul() {
    this.finalResults = parseFloat(this.state.inputFirst) * parseInt(this.state.inputSecond);
    console.log(this.finalResults);
    this.setState({
      finalresult: this.finalResults
    })
  }

  div() {
    this.finalResults = parseFloat(this.state.inputFirst) / parseInt(this.state.inputSecond);
    console.log(this.finalResults);
    this.setState({
      finalresult: this.finalResults
    })
  }

  mod() {
    this.finalResults = parseInt(this.state.inputFirst) % parseInt(this.state.inputSecond);
    console.log(this.finalResults);
    this.setState({
      finalresult: this.finalResults
    })
  }

  hideshow() {
    this.toggle = !this.toggle;
    this.setState({
      hide: this.toggle
    });
  }

  scientificCal(event) {
    this.ScientificValue = event.target.name;
    this.inputFirst = this.ScientificValue;
    this.setState({
      finalresult: this.inputFirst
    })
  }

  sin() {
    this.ScientificResult = Math.sin(parseInt(this.inputFirst.slice(3, this.inputFirst.lenth)));
    this.finalResults = this.ScientificResult.toString().slice(0, 8);
    this.setState({
      finalresult: this.ScientificResult
    })
  }

  cos() {
    this.ScientificResult = Math.cos(parseInt(this.inputFirst.slice(3, this.inputFirst.lenth)));
    this.finalResults = this.ScientificResult.toString().slice(0, 8);
    this.setState({
      finalresult: this.ScientificResult
    })
  }

  log() {
    this.ScientificResult = Math.log(parseInt(this.inputFirst.slice(3, this.inputFirst.lenth)));
    this.finalResults = this.ScientificResult.toString().slice(0, 8);
    this.setState({
      finalresult: this.ScientificResult
    })
  }
  tan() {
    this.ScientificResult = Math.tan(parseInt(this.inputFirst.slice(3, this.inputFirst.lenth)));
    this.finalResults = this.ScientificResult.toString().slice(0, 8);
    this.setState({
      finalresult: this.ScientificResult
    })
  }

  sqrt() {
    this.ScientificResult = Math.sqrt(parseInt(this.inputFirst.slice(1, this.inputFirst.lenth)));
    console.log(this.ScientificResult)
    this.finalResults = this.ScientificResult;
    this.setState({
      finalresult: this.ScientificResult
    })
  }

  pi() {
    this.ScientificResult = Math.PI;
    this.finalResults = this.ScientificResult;
    this.setState({
      finalresult: this.ScientificResult
    })
  }

  cbrt() {
    this.ScientificResult = Math.cbrt(parseInt(this.inputFirst.slice(1, this.inputFirst.lenth)));
    this.finalResults = this.ScientificResult;
    this.setState({
      finalresult: this.ScientificResult,
    })
  }

  power() {
    if (this.inputFirst !== "") {
      this.finalResults = Math.pow(this.inputFirst, this.inputSecond);
      this.setState({
        finalresult: this.finalResults
      })
    }
  }
  render() {
    let jsx = this.state.hide ? <><Input result={this.state.result} finalResult={this.state.finalresult} />
      <Operation takedata={this.takeData.bind(this)} takedata2={this.takeData2.bind(this)} opr={this.operator.bind(this)} back={this.backSpace.bind(this)} back2={this.backSpace2.bind(this)} cleardata={this.clearData.bind(this)} cleardata2={this.clearData2.bind(this)} equalbtn={this.equal.bind(this)} toggle={this.hideshow.bind(this)} /></> : <><Input result={this.state.result} finalResult={this.state.finalresult} />
        <Operation takedata={this.takeData.bind(this)} takedata2={this.takeData2.bind(this)} opr={this.operator.bind(this)} back={this.backSpace.bind(this)} back2={this.backSpace2.bind(this)} cleardata={this.clearData.bind(this)} cleardata2={this.clearData2.bind(this)} equalbtn={this.equal.bind(this)} toggle={this.hideshow.bind(this)} /><Scientific callScientific={this.scientificCal.bind(this)} opr={this.operator.bind(this)} /></>
    return (
      <div>
        {jsx}
      </div>
    )
  }
}
