import React from 'react';
export const Operation = (props) => {
  return (
    <div className="row">
      <div className="left">
        <div className="calc1">
          <button onClick={props.cleardata}>AC</button>
          <button onClick={props.back}>CE</button>
          <button name="%" onClick={(event) => {
            props.takedata(event.target.name)
          }}>%</button>
          <button className="btncolor" name="/" onClick={(event) => {
            props.takedata(event.target.name)
          }}>/</button><br />
          <button name="7" onClick={(event) => {
            props.takedata(event.target.name)
          }}>7</button>
          <button name="8" onClick={(event) => {
            props.takedata(event.target.name)
          }}>8</button>
          <button name="9" onClick={(event) => {
            props.takedata(event.target.name)
          }}>9</button>
          <button className="btncolor" name="*" onClick={(event) => {
            props.takedata(event.target.name)
          }}>x</button><br />
          <button name="4" onClick={(event) => {
            props.takedata(event.target.name)
          }}>4</button>
          <button name="5" onClick={(event) => {
            props.takedata(event.target.name)
          }}>5</button>
          <button name="6" onClick={(event) => {
            props.takedata(event.target.name)
          }}>6</button>
          <button className="btncolor" name="-" onClick={(event) => {
            props.takedata(event.target.name)
          }}>-</button><br />
          <button name="1" onClick={(event) => {
            props.takedata(event.target.name)
          }}>1</button>
          <button name="2" onClick={(event) => {
            props.takedata(event.target.name)
          }}>2</button>
          <button name="3" onClick={(event) => {
            props.takedata(event.target.name)
          }}>3</button>
          <button className="btncolor" name="+" onClick={(event) => {
            props.takedata(event.target.name)
          }}>+</button><br />
          <button>C</button>
          <button name="0" onClick={(event) => {
            props.takedata(event.target.name)
          }}>0</button>
          <button name="." onClick={(event) => {
            props.takedata(event.target.name)
          }}>.</button>
          <button className="btnequal" name="=" onClick={(event) => {
            props.takedata(event.target.name)
          }}>=</button>
        </div>
      </div>
      <div className="right">
        <div className="calc2">
          <button onClick={props.cleardata2}>AC</button>
          <button onClick={props.back2}>CE</button>
          <button name="%" onClick={props.opr}>%</button>
          <button className="btncolor" name="/" onClick={props.opr}>/</button><br />
          <button name="7" onClick={(event) => {
            props.takedata2(event.target.name)
          }}>7</button>
          <button name="8" onClick={(event) => {
            props.takedata2(event.target.name)
          }}>8</button>
          <button name="9" onClick={(event) => {
            props.takedata2(event.target.name)
          }}>9</button>
          <button className="btncolor" name="*" onClick={props.opr}>x</button><br />
          <button name="4" onClick={(event) => {
            props.takedata2(event.target.name)
          }}>4</button>
          <button name="5" onClick={(event) => {
            props.takedata2(event.target.name)
          }}>5</button>
          <button name="6" onClick={(event) => {
            props.takedata2(event.target.name)
          }}>6</button>
          <button className="btncolor" name="-" onClick={props.opr}>-</button><br />
          <button name="1" onClick={(event) => {
            props.takedata2(event.target.name)
          }}>1</button>
          <button name="2" onClick={(event) => {
            props.takedata2(event.target.name)
          }}>2</button>
          <button name="3" onClick={(event) => {
            props.takedata2(event.target.name)
          }}>3</button>
          <button className="btncolor" name="+" onClick={props.opr}>+</button><br />
          <button onClick={props.toggle}><i className="fas fa-calculator"></i></button>
          <button name="0" onClick={(event) => {
            props.takedata2(event.target.name)
          }}>0</button>
          <button name="." onClick={(event) => {
            props.takedata2(event.target.name)
          }}>.</button>
          <button className="btnequal" onClick={() => {
            props.equalbtn("=")
          }}>=</button>
        </div>
      </div>


    </div>

  )
}