import React from 'react';
import '../Css/main.css';
export const Input = (props) => {
  return (
    <>
      <div className="row">
        <div className="left">
          <p className="eval">With Eval - arithmetic</p>
          <h1 className="ipop">{props.result.toString().slice(0, 16)}</h1>

        </div>
        <div className="right">
          <p className="weval">Without Eval - scientific</p>
          <h1 className="ipop2">{props.finalResult.toString().slice(0, 16)}</h1>
        </div>
      </div>
    </>
  )
}